.friend__item {
    display: flex;
    gap: 20px;
    margin-bottom: 10px;
    align-items: center;
    border: 1px solid rgb(186, 181, 181);
    border-radius: 5px;
    padding: 10px;
    background-color: #fff;
    box-shadow: 4px 1px 4px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06),
        1px 4px 6px rgba(0, 0, 0, 0.16);
}

.friend__online,
.friend__offline {
    border-radius: 50%;
    width: 20px;
    height: 20px;
}

.friend__online {
    background-color: green;
}

.friend__offline {
    background-color: red;
}

.friend__avatar {
    border-radius: 2px;
}

.friend__name {
    font-weight: 700;
    font-size: 18px;
}