.stat {
    width: 300px;
    background-color: #fff;
    text-align: center;
    box-shadow: 4px 1px 4px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06),
        1px 4px 6px rgba(0, 0, 0, 0.16);
    border-radius: 8px;
    overflow: hidden;
}

.stat__title {
    padding: 20px 0;
    margin: 0 auto;
    text-transform: uppercase;
}

.stat__list {
    display: flex;
}

.stat__item {
    width: calc(100% / 5);
    display: flex;
    flex-direction: column;
    padding: 10px 0;
}

.stat__label {
    color: white;
}

.stat__percentage {
    font-size: 20px;
    color: white;
}